.loading {
    color: red;
    align-self: center;
}

.threaditem {
    margin-top: 1rem;
    width: 90%;
    margin-left: 5%;
    border: 1px solid blue;
    padding: 0.3rem;
    border-radius: 1rem;
}

.thread-heading {
    display: flex;
}

.thread-heading img {
    margin-right: 1rem;
    padding: 0.2rem;
}

.thread-heading .creatorContainer {
    display: block;
    width: 100%;
    padding-right: 1rem;
}

.thread-heading .creator {
    display: flex;
    justify-content: space-between;
}

.creator-name {
    font-weight: bold;
    cursor:default;
}

.creator-name, .thread-title, .thread-date {
    font-size: 18px;
}
.thread-date {
    font-weight: 300;
}

.thread-description {
    width: 90%;
    margin-left: 5%;
    font-size: 18px;
    line-height: 24px;
}

.thread-options-container {
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 5%;
    margin-top: 10px;
    width: 90%;
}
.thread-options-container div {
    font-size: 18px;
    color: blue;
    text-decoration: underline;
    cursor:pointer;
}

#open-thread {
    align-self: center;
    text-align: center;
    color: blue;
    text-decoration: underline;
    font-size: 20px;
    margin-bottom: 12px;
    margin-top: 12px;
}

@media screen and (min-width: 512px) {
    .threaditem {
        width: 80%;
        margin-left: 10%;
    }
    .creator-name, .thread-title, .thread-date, .thread-description  {
        font-size: 14;
    }
  }
  
  @media screen and (min-width: 720px) {
    .threaditem  {
        width: 65%;
        margin-left: 17.5%;
    }
  }
  
  @media screen and (min-width: 980px) {
    .threaditem  {
        width: 50%;
        margin-left: 25%;
    }
  }