header {
    background-color: rgba(115, 170, 220, 1);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.5rem;
}
  
header h1 {
    font-size:  x-large;
    margin-left: 1rem;
    align-self: center;
}

header input {
    align-self: center;
    margin-right: 1rem;
    font-size: 1rem;
    height: 2rem;
    width: 10rem;
    border: none; 
    border:solid 1px rgb(64, 108, 150);
    border-radius: 10px;
    padding-left: 12px;
}
  
@media screen and (min-width: 600px) {
    header input {
        width: 12rem;
    }
}